body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    text-align:center;
    margin:auto;
}
#title{
    text-align: center;
}
#tooltip {
    text-align: center;	
    margin:auto;		
    margin-top:20px;
    margin-bottom:20px;
    width: 180px;					
    height: 32px;					
    padding: 2px;				
    font: 14px sans-serif;		
    background: white;	
    border: 0px;		
    border-radius: 8px;			
    pointer-events: none;	
    visibility: hidden;	
    ;
  }
  li{
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
  }
  #select-box{
      margin-top:50px;
        text-align:center
  }
  #selectButton{
      text-align: center;
  }
  #svg-div{
      margin:auto;
      text-align: center;
  }
.landing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    grid-gap:1vh;
    gap:1vh;
}


